// Nav.js
// This component renders the navigation bar for the application, with custom styles and responsive behavior.

import React from "react";
import { useSelector } from "react-redux";
import "./Nav.css"; // Import custom CSS for additional styling

/**
 * Nav Component
 * 
 * The Nav component renders a responsive navigation bar that includes buttons to navigate to different parts of the application.
 * It displays the total number of items in the cart, sourced from the Redux store.
 * 
 * @param {Object} props - Component props.
 * @param {Function} props.setView - Function to change the current view.
 */
export default function Nav({ setView }) {
  const total = useSelector((state) => state.totalCart); // Access the total number of items in the cart from the Redux store

  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light" style={style.nav}>
      <div className="container-fluid">
        {/* Brand Logo and Name */}
        <a className="navbar-brand" href="#!" style={style.navHead}>
          <img
            src="https://cdn-icons-png.flaticon.com/512/833/833300.png"
            alt="logo"
            width="30rem"
            className="me-2"
          />
          Shopify
        </a>

        {/* Toggle button for collapsing navbar content on smaller screens */}
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        {/* Collapsible Navbar Links */}
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            {/* Button to navigate to Products Page */}
            <li className="nav-item">
              <button
                className="nav-link"
                style={style.navLink}
                onClick={() => setView("products")}
              >
                Products
              </button>
            </li>
            {/* Button to navigate to Add Product Page */}
            <li className="nav-item">
              <button
                className="nav-link"
                style={style.navLink}
                onClick={() => setView("addProduct")}
              >
                Add a Product
              </button>
            </li>
          </ul>

          {/* Icons for Cart and Profile */}
          <div className="d-flex gap-3 position-relative align-items-center">
            {/* Cart Icon with Badge showing Total Items in Cart */}
            <div className="icon-wrapper" onClick={() => setView("cart")}>
              <img
                src="https://cdn-icons-png.flaticon.com/512/4290/4290854.png"
                alt="cart"
                width={"30rem"}
                style={style.icon}
                title="View Cart"
              />
              {total ? (
                <span
                  className="badge rounded-circle position-absolute d-flex align-items-center justify-content-center"
                  style={style.cartBadge}
                >
                  {total}
                </span>
              ) : null}
            </div>
            {/* User Profile Icon */}
            <div className="icon-wrapper">
              <img
                src="https://cdn-icons-png.flaticon.com/512/236/236832.png"
                alt="profile"
                width={"30rem"}
                style={style.icon}
                title="User Profile"
              />
            </div>
          </div>
        </div>
      </div>
    </nav>
  );
}

// Inline styles for various elements within the Navbar
const style = {
  nav: {
    backgroundImage: "linear-gradient(135deg, #f6d365 0%, #fda085 100%)", // Gradient background for navbar
  },
  navHead: {
    fontFamily: "var(--fontStyle)",
    color: "#fff", // White text for better contrast against the background
    display: "flex",
    alignItems: "center",
  },
  navLink: {
    fontSize: "1rem", // Adjust font size for better readability
    transition: "color 0.3s ease", // Smooth transition effect for link colors
    marginRight: "1rem", // Margin between links
    border: "none", // Remove button border
    background: "none", // Remove button background
    cursor: "pointer", // Pointer cursor for buttons
    color: "#333", // Default color for the links
  },
  icon: {
    cursor: "pointer", // Pointer cursor to indicate clickable icons
    transition: "transform 0.3s ease, opacity 0.3s ease", // Smooth transition for hover effects
  },
  cartBadge: {
    width: "1.5rem", // Size of the badge displaying cart count
    height: "1.5rem",
    top: "5px", // Positioning relative to cart icon
    left: "18px",
    backgroundColor: "#ff6f61", // Color for the badge background
    color: "#fff", // Color for the badge text
    fontSize: "0.8rem", // Font size for the cart count
  },
};
