import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import Nav from "./Component/Nav";
import ProductDetail from "./Component/ProductDetail";
import AddProduct from "./Component/AddProduct";
import CartItems from "./Component/CartItems";
import ProductItemList from "./Component/ProductItemList";
import { addproducts } from "./actions/index";
import customFetch from "./apiCall";

/**
 * Main application component that manages different views of the e-commerce app.
 */
function App() {
  const [view, setView] = useState("products"); // State to manage the current view
  const productDetailItem = useSelector((state) => state.itemToDisplay); // Access selected product for detailed view
  const dispatch = useDispatch(); // Hook to dispatch actions to Redux store

  const url = "https://my-json-server.typicode.com/jaiswalaryan/data/db"; // API endpoint URL

  useEffect(() => {
    const fetchData = async () => {
      const data = await customFetch(url, { method: "GET" });
      const modifiedData = data.products.map((item) => ({
        ...item,
        edit: true,
      }));
      window.localStorage.setItem("products", JSON.stringify(modifiedData)); // Store products in local storage
      dispatch(addproducts(modifiedData)); // Dispatch action to add products to Redux store
    };

    fetchData(); // Fetch and set products data on component mount
  }, [dispatch]);

  // Function to render the appropriate component based on current view state
  const renderView = () => {
    switch (view) {
      case "products":
        return <ProductItemList setView={setView} />;
      case "addProduct":
        return <AddProduct setView={setView} />;
      case "productDetail":
        return <ProductDetail item={productDetailItem} setView={setView} />;
      case "cart":
        return <CartItems setView={setView} />;
      default:
        return <ProductItemList setView={setView} />;
    }
  };

  return (
    <div className="App">
      <Nav setView={setView} /> {/* Pass setView to Nav for handling navigation */}
      {renderView()} {/* Render the current view component */}
    </div>
  );
}

export default App;
